import React, { useState } from 'react';
import qrcode from 'qrcode-generator';
import { jsPDF } from 'jspdf';

const QRCodeDownloader = () => {
    const [startNumber, setStartNumber] = useState('');
    const [length, setLength] = useState('');
    const [size, setSize] = useState(100); // Default size in pixels
    const [xPosition, setXPosition] = useState(300); // Default x position in pixels
    const [yPosition, setYPosition] = useState(300); // Default y position in pixels

    const generateQRCode = () => {
        const start = parseInt(startNumber);
        const len = parseInt(length);

        if (!isNaN(start) && !isNaN(len) && len > 0) {
            const pdf = new jsPDF('p', 'px', 'a4'); // Create a new PDF document

            for (let i = 0; i < len; i++) {
                const number = start + i;
                const text = `RLSI${number}`;

                let qr = qrcode(0, 'L');
                qr.addData(text);
                qr.make();

                let qrCanvas = document.createElement('canvas');
                let ctx = qrCanvas.getContext('2d');
                qrCanvas.width = size;
                qrCanvas.height = size;
                let tileW = qrCanvas.width / qr.getModuleCount();
                let tileH = qrCanvas.height / qr.getModuleCount();

                for (let row = 0; row < qr.getModuleCount(); row++) {
                    for (let col = 0; col < qr.getModuleCount(); col++) {
                        ctx.fillStyle = qr.isDark(row, col) ? '#000000' : '#ffffff';
                        ctx.fillRect(col * tileW, row * tileH, tileW, tileH);
                    }
                }

                let dataURL = qrCanvas.toDataURL('image/png');

                const pdfWidth = pdf.internal.pageSize.getWidth();
                const pdfHeight = pdf.internal.pageSize.getHeight();
                const xPixels = (xPosition * pdfWidth) / 210; // 210mm is A4 width
                const yPixels = (yPosition * pdfHeight) / 297; // 297mm is A4 height
                const sizePixels = (size * pdfWidth) / 210; // Convert size to pixels relative to A4 width
                if (i > 0) {
                    pdf.addPage(); // Add a new page for each QR code after the first one
                }
                pdf.addImage(dataURL, 'PNG', xPixels, yPixels, sizePixels / 2, sizePixels / 2); // Set position and size in the PDF
            }

            pdf.save(`GradeCard.pdf`); // Save the PDF document
        }
    };

    return (
        <div>
            <h1>QR Code to PDF Downloader</h1>
            <div>
                <label>
                    Start Number:
                    <input
                        type="number"
                        value={startNumber}
                        onChange={(e) => setStartNumber(e.target.value)}
                    />
                </label>
            </div>
            <div>
                <label>
                    Length:
                    <input
                        type="number"
                        value={length}
                        onChange={(e) => setLength(e.target.value)}
                    />
                </label>
            </div>
            <div>
                <label>
                    QR Code Size (pixels):
                    <input
                        type="number"
                        value={size}
                        onChange={(e) => setSize(e.target.value)}
                    />
                </label>
            </div>
            <div>
                <label>
                    X Position (in PDF, pixels):
                    <input
                        type="number"
                        value={xPosition}
                        onChange={(e) => setXPosition(e.target.value)}
                    />
                </label>
            </div>
            <div>
                <label>
                    Y Position (in PDF, pixels):
                    <input
                        type="number"
                        value={yPosition}
                        onChange={(e) => setYPosition(e.target.value)}
                    />
                </label>
            </div>
            <button onClick={generateQRCode}>
                Download QR Code as PDF
            </button>
        </div>
    );
};

export default QRCodeDownloader;
